import React, { Component } from 'react'
import PropTypes from 'prop-types'
import H5Header from '../../components/H5Header'
import H5FSProductContainer from '../../containers/H5FSProductContainer'
import H5FSPreviewContainer from '../../containers/H5FSPreviewContainer'
import H5FSDetailContainer from '../../containers/H5FSDetailContainer'
import H5ElementStructureContainer from '../../containers/H5ElementStructureContainer'
import './H5MobileEditor.scss'
import './index.scss'

const LeftSideBar = ({ pageId }) => (
  <div className="sidebar-left" >
    <H5FSProductContainer pageId={pageId} />
    {/*
    <a className="btn-more" href="wandaffoap://jump/componentMarket" >
      <i className="iconfont icon-puzzle" />
      <span className="text" ></span>
      <i className="iconfont icon-readMore" />
    </a>
    */}
  </div>
)

export class Editor extends Component {
  render() {
    const { pageID } = this.props.params
    return <div className="edit-shop-warp" >
      <H5Header />
      <div className="edit-shop-container" >
        <LeftSideBar pageId={pageID} />
        <div className="editor-content" >
          <div className="editor-preview-content"><H5FSPreviewContainer /></div>
          <H5ElementStructureContainer />
        </div>
        <div className="h5-sidebar-right" >
          <H5FSDetailContainer pageId={pageID} />
        </div>
      </div>
    </div>
  }
}

export default Editor

Editor.propTypes = {
  params: PropTypes.object,
  //editor: PropTypes.object.isRequired,
}



