import fetchUtil from 'utils/fetchUtil'
import { fixDomain, getDomain } from 'utils/domain'
import { defaultProducts } from './__productData'
const SELECT_PRODUCT = 'ffan/SELECT_PRODUCT'
const CHANGE_PRODUCT = 'ffan/CHANGE_PRODUCT'
const CHANGE_BASIC_INFO = 'ffan/CHANGE_BASIC_INFO'
const UPDATE_VIEWDATA = 'ffan/UPDATE_VIEWDATA'
const CHANGE_TAB = 'ffan/CHANGE_TAB'
const CHANGE_MODAL_STATE = 'ffan/CHANGE_MODAL_STATE'
const MERGE_PAGE_INFO = 'ffan/MERGE_PAGE_INFO'
//const GET_PRODUCTS = 'ffan/GET_PRODUCT'

// TODO redux
