//  return defaultProducts
//}

const setBasicInfoComponent = (elements, basic) => {
  return elements.map(ele => {
    if (ele.type && (ele.type.includes('BasicInfo') || ele.needInfo)) {
      ele.templateData = basic
    }
    return ele
  })
}

export const getCurrentStore = pageId => dispatch => {
  return Promise.all([
    fetchPreview(pageId).then(data => {
      return data
    }),
    fetchViewData(pageId).then(({ basic, viewData }) => {
      if (basic) {
        dispatch({
          type: CHANGE_BASIC_INFO,
          basicInfo: basic,
        })
      }
      // TODO: dispatch
      // storeViewData = viewData
      dispatch(updateViewData(viewData))
    }, () => {
      // TODO : 
    })
  ])
}

export const fetchPreview = (pageID) => {
  const apiUrl = getDomain(`/app/v1/bo/v1/web/merchant/page/${pageID}`)
  return fetchUtil.getJSON(apiUrl).then(data => {
    return data
  })['catch'](e => {
    console.warn(e)
  })
}

export const fetchViewData = pageId => {
  return new Promise((resolve, reject) => {
    const apiUrl = fixDomain(`http://api.ffan.net/bob/v1/decorate/basic/${pageId}`)
    fetchUtil.getJSON(apiUrl).then(
      res => {
        const { redecorate: { viewData }, basic } = res
        // viewData 
        if (!viewData) {
          // TODO : resolve or reject ?
          //return resolve({})
          alert('viewData')
        }
        if (basic) {
          viewData.elements = setBasicInfoComponent(viewData.elements, basic)
          viewData.elements.map(ele => {
            if (Array.isArray(ele.value)) {
              return ele.value.map(eleItem => {
                const img = Object.assign({}, eleItem)
                eleItem.initImg = img.image
                return eleItem
              })
            }
          })
          document.title = basic.storeName
        }
        return resolve({ basic, viewData })
      }
    ).catch(e => {
      console.log(e)
      return reject(e)
    })
  })
}

export const updateViewData = viewData => dispatch => {
  dispatch({
    type: UPDATE_VIEWDATA,
    selectedProduct: {
      elements: viewData.elements,
      selectedTemplateId: viewData.selectedTemplateId
    }
  })
  dispatch({
    type: CHANGE_PRODUCT,
    selectedProduct: {
      elements: viewData.elements,
      selectedTemplateId: viewData.selectedTemplateId
    }
  })
  dispatch({
    type: SELECT_PRODUCT,
    selectedTemplateId: viewData.selectedTemplateId,
    activeIndex: 1,
  })
}

export const selectProduct = product => (dispatch, getState) => {
  dispatch({
    type: SELECT_PRODUCT,
    selectedTemplateId: product.id,
  })

  dispatch({
    type: CHANGE_TAB,
    activeIndex: 2,
  })

  const products = getState().fsProduct.products
  // const selectedProduct = (product.id === storeViewData.selectedTemplateId) ? {
  //   //  // TODO: 
  //   elements: storeViewData.elements,
  //   id: storeViewData.id,
  //   selectedTemplateId: storeViewData.id,
  // } : products.find(prod => prod.id === product.id)

  // TODO debug
  let selectedProduct = products.find(prod => prod.id === product.id)
  const basicInfo = getBasicInfo(getState())
  if (basicInfo) {
    selectedProduct.elements = setBasicInfoComponent(selectedProduct.elements, basicInfo)
  }
  dispatch({
    type: CHANGE_PRODUCT,
    selectedProduct,
  })
}

const getBasicInfo = state => {
  if (state && state[ 'fsProduct' ] && state[ 'fsProduct' ][ 'basicInfo' ]) {
    return state[ 'fsProduct' ][ 'basicInfo' ]
  } else {
    return null
  }
}

export const mergePageInfo = (pageInfo) => (dispatch, getState) => {
  dispatch({
    type: MERGE_PAGE_INFO,
    pageInfo
  })
}

export const changeModalState = (modalState) => (dispatch, getState) => {
  dispatch({
    type: CHANGE_MODAL_STATE,
    modalState
  })
}

const ACTION_HANDLERS = {
  [SELECT_PRODUCT]: (state, { selectedTemplateId, activeIndex }) => {
    const products = state.products.map(prod => (
      { ...prod, selected: selectedTemplateId === prod.id }))
    return { ...state, products, activeIndex }
  },
  [CHANGE_BASIC_INFO]: (state, { basicInfo }) => ({
    ...state, basicInfo
  }),
  [UPDATE_VIEWDATA]: (state, { selectedProduct }) => {
    const products = state.products.map(prod => {
      if (prod.id === selectedProduct.selectedTemplateId) {
        return { ...prod, elements: selectedProduct.elements }
      } else {
        return { ...prod }
      }
    })
    return { ...state, products }
  },
  [CHANGE_MODAL_STATE]: (state, { modalState }) => {
    return {
      ...state,
      modalState
    }
  },
  [MERGE_PAGE_INFO]: (state, { pageInfo }) => {
    return {
      ...state,
      pageInfo
    }
  }
}

const initState = {
  products: defaultProducts,
  modalState:false
}

export default function fsProductReducer(state = initState, action) {
  const handler = ACTION_HANDLERS[ action.type ]
  return handler ? handler(state, action) : state
}



