import { connect } from 'react-redux'
import { compose } from 'redux'
import { DragDropContext } from 'react-dnd'
import TouchBackend from 'react-dnd-touch-backend'
import HTML5Backend from 'react-dnd-html5-backend'
import device from 'utils/device'
import * as product from '../modules/fsProduct'
import PreviewSettings from '../components/H5FSDetail/PreviewSettings'
const dndBackend = device.isMobile() ? DragDropContext(TouchBackend) : DragDropContext(HTML5Backend)

const mapStateToProps = ({ previewData, fsProduct }) => ({
  ...previewData,
  modalState: fsProduct.modalState
})

const mapDispatchToProps = {
  ...product
}

export default compose(
  dndBackend,
  connect(mapStateToProps, mapDispatchToProps),
)(PreviewSettings)



