import React from 'react'
import './PreviewSettings.scss'
import DraggableElement from 'h5editor/containers/DraggableElementContainer'
import { Button } from 'antd'

export default class PreviewSettings extends React.Component {
  render() {
    const { elements } = this.props
    return <div className="preview-settings-container" >
      <div className="sortable-container" >
        <h2 className="detail-title"></h2>
        {elements.map(ele => <DraggableElement {...ele} />)}
        <h2 className="detail-title"></h2>
        <Button onClick={() => this.props.changeModalState(true)}></Button>
      </div>
    </div>
  }
}



