import React from 'react'
import { Form, Modal, Input } from 'antd'
const FormItem = Form.Item

const WeChatCreateForm = Form.create()(
  (props) => {
    const { visible, onCancel, onCreate, form } = props
    const { getFieldDecorator } = form

    return (
      <Modal
        visible={visible}
        title=""
        okText=""
        onCancel={onCancel}
        onOk={onCreate}
      >
        <Form layout="horizontal" >
          <FormItem label={''}>
            {getFieldDecorator('title', {
              initialValue: ''
            })(< Input />)}
          </FormItem>
          <FormItem label={''}>
            {getFieldDecorator('desc', {
              initialValue: ''
            })(< Input />)}
          </FormItem>
          <FormItem label={''}>
            {getFieldDecorator('imgUrl', {
              initialValue: 'http://timg.ffan.com/convert/resize/url_T1yTZTB_AX1RCvBVdK/tfs/1.png'
            })(< Input />)}
          </FormItem>
        </Form>
      </Modal>
    )
  }
)

export default WeChatCreateForm



