import React from 'react'

export const ElementContainer = (props, { fetchRemote }) => (WrappedComponent) => {
  class Element extends React.Component {
    componentDidMount() {
      const { remotes } = props
      if (remotes) {
        fetchRemote(remotes)
      }
    }

    render() {
      return <WrappedComponent {...props} />
    }
  }
  return <Element />
}



