import React from 'react'
import Container from '../../../Elements/container'
import Elements from '../../../Elements'
import './detailStyle.scss'

const getRadioComponent = (ele, onChange) => (
  <li className="ele" >
    <h2 className="ele-title" >{ele.name}</h2>
    <div className="ele-content" >
      {ele.items.map(item => <div className="item" style={ele.styleDisplay} key={ele.id + item.value} onClick={
        e => onChange({ id: ele.id, value: item.value, })} >
        <img className="item-img" style={ele.size} src={item.previewImage} alt="" />
        <div className="row-radio" >
          <input type="radio" checked={ele.value === item.value}
                 onChange={e => onChange({
                   id: ele.id, value: item.value
                 })} className="radio-input" />
          <span className="radio-btn">
            <i className="iconfont icon-radio1" />
            <i className="iconfont icon-radio2" />
          </span>
          {item.name}
        </div>
      </div>)}
    </div>
  </li>
)

const getInputComponent = (ele, onChange) => (
  <li className="item" >
    <div className="ele-title" >{ele.name}</div>
    <div className="ele-content" >
      <input type="text" className="item-input" value={ele.value} onChange={
        e => onChange({ id: ele.id, value: e.target.value })} />
    </div>
  </li>
)

export const getElement = (ele, onChange) => {
  if (Container[ ele.type ]) {
    const { Preference } = Container[ ele.type ]
    return <Preference {...ele} onChange={onChange} />
  }

  if (Elements[ ele.type ]) {
    const { Preference } = Elements[ ele.type ]
    return <Preference {...ele} onChange={onChange} />
  }

  if (ele.type === 'radio' || ele.type === 'imageLink') {
    return getRadioComponent(ele, onChange)
  } else if (ele.type === 'input') {
    return getInputComponent(ele, onChange)
  } else if (ele.type && ele.type.includes('radio') && ele.type.includes('basicInfo')) {
    // radio 
    return getRadioComponent(ele, onChange)
  }
}



