import { connect } from 'react-redux'
import H5ElementStructs from '../components/H5ElementStrct/H5ElementStruct'
import * as eleStructActions from '../modules/eleStruct'

const mapStateToProps = ({ eleStructData }) => ({
  elementStructs: eleStructActions.getElementStructs(eleStructData)
})

const mapDispatchToProps = {
  ...eleStructActions,
}

export default connect(mapStateToProps, mapDispatchToProps)(H5ElementStructs)



