import remoteInterface from 'h5editor/api/remoteInterface'
const RESPONSE_REMOTE = 'ffan/RESPONSE_REMOTE'
const RESPONSE_REMOTE_ERROR = 'ffan/RESPONSE_REMOTE_ERROR'

const responseRemoteSuccess = (data, remote) => ({
  type: RESPONSE_REMOTE,
  data,
  remote,
})

const responseRemoteError = (error, remote) => ({
  type: RESPONSE_REMOTE_ERROR,
  error,
  remote
})

const requestRemote = ({ dataType, params }) => {
  const func = remoteInterface[ dataType ]
  if (typeof func === 'function') {
    return func(params)
  }
  return Promise.reject(new Error('not found dataType'))
}

const responseRemote = (remote) => dispatch =>
  requestRemote(remote).then(
    data => dispatch(responseRemoteSuccess(data, remote)),
    error => dispatch(responseRemoteError(error, remote))
  )

export const fetchRemote = (remotes = []) => dispatch => Promise.all(
  remotes.map(remote => dispatch(responseRemote(remote)))).then(() => {
    console.log('After fetch remotes')
  }
)

export default function remoteHandler(state = {}, action) {
  switch (action.type) {
    case RESPONSE_REMOTE:
      return { ...state, [action['remote']['dataType']] : action.data }
    default:
      return { ...state }
  }
}

