import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import H5FSPreview from '../components/H5FSPreview'
import { selectElement, setRef, stepMoveElement } from '../modules/fsPreview'
import { fetchRemote } from '../modules/remotes'

const mapStateToProps = ({ previewData }) => ({
  ...previewData,
})

// https://stackoverflow.com/questions/34458261/how-to-get-simple-dispatch-from-this-props-using-connect-w-redux

const mapDispatchToProps = dispatch => bindActionCreators({
  selectElement,
  setRef,
  stepMoveElement,
  fetchRemote,
  dispatch,
}, dispatch)

export default connect(mapStateToProps, mapDispatchToProps)(H5FSPreview)



