import { compose } from 'redux'
import { connect } from 'react-redux'
import { DragSource, DropTarget } from 'react-dnd'
import { moveElement, removeElementStruct } from 'h5editor/modules/fsPreview'

import DraggableElement from '../components/H5FSDetail/PreviewSettings/DraggableElement'

const elementSource = {
  beginDrag(props) {
    return {
      id: props.id,
    }
  },
}
const elementTarget = {
  hover(props, monitor) {
    const { id: draggedId } = monitor.getItem()
    const { id: overId, dispatch } = props
    if (draggedId !== overId) {
      dispatch(moveElement({ fromId: draggedId, toId: overId }))
    }
  }
}

const mapDispatchToProps = {
  dispatch: (dispatch) => dispatch,
  removeElementStruct: (id) => (dispatch) => {
    dispatch(removeElementStruct(id))
  }
}

export default compose(
  connect(null, mapDispatchToProps),
  DragSource('element', elementSource, (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
  })),
  DropTarget('element',
    elementTarget, (connect, monitor) => ({
      connectDropTarget: connect.dropTarget(),
    })
  ),
)(DraggableElement)



