import fetchUtil from 'utils/fetchUtil'
import { getDomain } from 'utils/domain'
import { fetchViewData, updateViewData } from '../modules/fsProduct'

const CHANGE_QR_CODE = 'ffan/CHANGE_QR_CODE'
const SAVE_WE_CHAT_CONFIG = 'ffan/SAVE_WE_CHAT_CONFIG'

// const URL_DEBUG_MODE = new RegExp('debug=1').test(location.search)

const publishPage = (pageId, viewData) => {
  const data = {
    elements: viewData.previewData.elements,
    selectedTemplateId: viewData.previewData.selectedTemplateId,
    weChatConfig: viewData.fsDetail.weChatConfig
  }
  return fetchUtil.postJSON(
    getDomain(`/app/v1/bo/v1/web/merchant/store/3/page/${pageId}/publish`), {
      viewData: data,
      pageType: 1
    },
    { formDataType: 'FormData' })
}

const fetchQRCodeIfNeeded = (pageId) => (dispatch, getState) => {
  const { qrData } = getState().fsDetail
  const qrCode = qrData[ getHomePage(pageId) ]
  if (qrCode) {
    return Promise.resolve(qrCode)
  } else {
    return dispatch(fetchQRCode(pageId))
  }
}

// const getHomePage = pageId => `http://bocxapi.intra.sit.ffan.com/boc/xapi/oauth/redirect?pageId=${pageId}
// ${URL_DEBUG_MODE ? '?debug=1' : ''}`
const getHomePage = pageId => `https://open.weixin.qq.com/connect/oauth2/authorize?` +
`appid=wx84052d6a8009c8e5&redirect_uri=http://open.ffan.net/merchant/home/${pageId}&` +
`response_type=code&scope=snsapi_base&state=ffan#wechat_redirect`
const fetchQRCode = (pageId) => (dispatch, getState) => {
  const homePage = getHomePage(pageId)
  console.log(homePage)
  const url = getDomain(`/app/v1/qrcode`, { suffix: 'com' })
  return new Promise((resolve, reject) => fetchUtil.getJSON(url, {
    type: 'png',
    size: 130,
    info: homePage
  }).then(
    data => {
      //data 
      if (Array.isArray(data)) {
        data = data[ 0 ]
      }
      dispatch({
        type: CHANGE_QR_CODE,
        qrCode: data,
        qrUrl: homePage
      })
      return resolve(data)
    }, err => {
      console.log('-- fetchQRCode error --', err)
      return reject(err)
    }
  ))
}

export const getQRCode = pageId => (dispatch, getState) => Promise.all([
  dispatch(fetchQRCodeIfNeeded(pageId)),
  publishPage(pageId, getState()).then(data => {
    fetchViewData(pageId).then(({ basic, viewData }) => {
      dispatch(updateViewData(viewData))
    })
  })
])

export const postWeChatConfig = (pageId, weChatConfig) => (dispatch, getState) => Promise.resolve(
  dispatch({
    type: SAVE_WE_CHAT_CONFIG,
    weChatConfig
  }))

const initState = {
  qrData: {
    'init': 'http://api.ffan.com/qrcode/v1/qrcode?' +
    'type=png&size=130&info=http://www.open.ffan.net'
  },
}

const ACTION_HANDLERS = {
  [CHANGE_QR_CODE]: (state, { qrCode, qrUrl }) => ({ ...state, qrData: { ...state.qrData, [qrUrl]: qrCode } }),
  [SAVE_WE_CHAT_CONFIG]: (state, { weChatConfig }) => ({ ...state, weChatConfig }),
}

export default function fsDetailReducer(state = initState, action) {
  const handler = ACTION_HANDLERS[ action.type ]
  return handler ? handler(state, action) : state
}



