import React, { Component } from 'react'
import classnames from 'classnames'

class ProductFilter extends Component {
  productsChange(data, bool) {
    const { selectProduct, switchShowHide } = this.props
    selectProduct(data)
    switchShowHide(bool)
  }

  renderFilter(products, onChange) {
    return products.map(product => <li
      className={classnames('fs-product', {
        selected: product.selected
      })}
      key={`product-${product.id}`}
      onClick={() => onChange({ id: product.id }, false)} >
      <div className="product-image" >
        <img className="preview-image" src={product.previewImage} alt="" />
        <div className="product-title" >
          <input type="radio" checked={product.selected} onChange={
            () => onChange({ id: product.id })
          } />
          <img src={product.selectedImg} className="selectedImg" />
        </div>
      </div>
      <span className="product-name" >{product.name}</span>
    </li>)
  }

  render() {
    return <div className="filter-container">
      <ul className="h5-fs-product" >
        {this.renderFilter(this.props.products, this.productsChange.bind(this))}
      </ul>
    </div>
  }
}

export default ProductFilter



