import { connect } from 'react-redux'
import H5FSProduct from '../components/H5FSProduct'
import * as products from '../modules/fsProduct'
const mapStateToProps = state => ({
  fsProduct: state.fsProduct
})

const mapDispatchToProps = {
  ...products,
}

export default connect(mapStateToProps, mapDispatchToProps)(H5FSProduct)



