import React, { Component } from 'react'
import './H5FSDetail.scss'
import { Tabs } from 'antd'
import Publisher from './Punblisher/Publisher'
import PreviewSettings from '../../containers/H5PreviewSettingContainer'
import { getElement } from './ElementPreference'
const TabPane = Tabs.TabPane

export class H5FSDetail extends Component {
  render() {
    const { element, activeIndex, changeTab, updateElement, ...rest } = this.props
    return <div className="h5-fs-detail" >
      <Tabs activeKey={`${activeIndex}`} onTabClick={c => changeTab(c)}>
        <TabPane tab="" key="1" >{getElement(element, updateElement)}
        </TabPane>
        <TabPane tab="" key="2" ><PreviewSettings /></TabPane>
      </Tabs>
      <Publisher {...rest} />
    </div>
  }
}

export default H5FSDetail



