import React, { Component } from 'react'
import './DragableElement.scss'
//import PropTypes from 'prop-types'

export default class DraggableElement extends Component {
  render() {
    const { connectDragSource, connectDropTarget, removeElementStruct, id, name } = this.props
    return connectDragSource(connectDropTarget(
      <div className="ele-draggable" >
        <i className="iconfont icon-del" onClick={() => removeElementStruct(id)} />
        {name}
        <i className="iconfont icon-rangeslider" />
      </div>
    ))
  }
}



