import { normalize } from 'normalizr'
import * as api from '../api'
import * as schema from './schema'
import { combineReducers } from 'redux'

const FETCH_ELEMENT_STRUCTURES = 'ffan/FETCH_ELEMENT_STRUCTURES'
const ADD_ELEMENT_STRUCTURE = 'ffan/ADD_ELEMENT_STRUCTURE'

const byId = (state = {}, action) => {
  if (action.response) {
    return {
      ...state,
      ...action.response.entities.elementStructs,
    }
  }
  return state
}

const getElementStruct = (state, id) => state[ id ]

export const addElementStructs = (id) => (dispatch, getState) => {
  const { eleStructData } = getState()
  const elementStruct = eleStructData.byId[ id ]
  dispatch({
    type: ADD_ELEMENT_STRUCTURE,
    elementStruct : Object.assign({}, elementStruct, { id: `ele-${+(new Date())}` })
  })

  // TODO: 
  return Promise.resolve(getState().previewData.refs)
}

export const fetchElementStructs = () => dispatch => {
  api.fetchElementStructs().then(response => {
    dispatch({
      type: FETCH_ELEMENT_STRUCTURES,
      response: normalize(response, schema.elements)
    })
  }, err => {
    console.log(err)
  })
}

export const createList = () => {
  const ids = (state = [], action) => {
    switch (action.type) {
      case FETCH_ELEMENT_STRUCTURES:
        return action.response.result
    }
    return state
  }

  return combineReducers({
    ids
  })
}

const getIds = state => state.ids

export const getElementStructs = (state) => {
  const ids = getIds(state.list)
  return ids.map(id => getElementStruct(state.byId, id))
}

export const elementStructures = combineReducers({
  byId,
  list: createList()
})

export default elementStructures



