import React, { Component } from 'react'
import classNames from 'classnames'
import Elements from '../../Elements'
import Container from '../../Elements/container'
import './index.scss'
import { ElementContainer } from '../../containers/ElementComtainer'

export default class H5FSPreview extends Component {
  renderElement(ele, fetchRemote) {
    if (Container[ ele.type ]) {
      const { Preview } = Container[ ele.type ]
      return ElementContainer(ele, { fetchRemote })(Preview)
    }

    if (Elements[ ele.type ]) {
      const { Preview } = Elements[ ele.type ]
      return ElementContainer(ele, { fetchRemote })(Preview)
    }
  }

  stepMoveElement = (id, step) => {
    const { stepMoveElement } = this.props
    stepMoveElement(id, step)
  }

  renderPreview({ elements, selectElement, selectedElementId, fetchRemote }) {
    if (!elements || !elements.length) {
      return <div>
        <img className="blank-bg" src="http://timg.ffan.com/convert/resize/url_T1fzEvByYg1RCvBVdK/tfs/1.png" />
      </div>
    }
    return elements.map(ele => <div
      onClick={e => selectElement(ele)}
      className={classNames('preview-ele', { 'selected-ele': ele.id === selectedElementId })} >
      {this.renderElement(ele, fetchRemote)}
      { ele.id !== selectedElementId ? '' : (
        <div className="fast-menu" >
          <button className="btn" onClick={() => { this.stepMoveElement(selectedElementId, -1) }} >
            <i className="iconfont icon-top-arrow" />
          </button>
          <button className="btn" onClick={() => { this.stepMoveElement(selectedElementId, 1) }} >
            <i className="iconfont icon-bottom-arrow" />
          </button>
        </div>
      )}
    </div>)
  }

  componentDidMount() {
    this.props.setRef({
      previewContainer: this.container
    })
  }

  render() {
    //console.log(this.props)
    return <div className="preview"
                ref={(container) => { this.container = container }} >
      {this.renderPreview(this.props)}
    </div>
  }
}



