import React, { Component } from 'react'
import { Modal as AntModal, Button } from 'antd'
import WeChatForm from '../WeChatForm'
import './Publisher.scss'

export class Publisher extends Component {
  state = {
    visible: false,
    lock: true
  }

  getWeChatModal = () => {
    this.setState({ visible: true })
  }
  handleCancel = () => {
    this.setState({ visible: false })
  }
  handleCreate = () => {
    const form = this.form
    form.validateFields((err, values) => {
      if (err) return
      const { pageId, postWeChatConfig } = this.props
      postWeChatConfig(pageId, values)
        .then(action => {
          form.resetFields()
          this.setState({ visible: false }, () => {
            this.previewPage(pageId)
          })
        })
    })
  }
  saveFormRef = (form) => {
    this.form = form
  }

  /**
   * 
   */
  previewPage = () => {
    const { getQRCode, pageId } = this.props
    if (!this.state.lock) {
      return
    }
    this.setState({
      lock: false
    })
    getQRCode(pageId)
      .then(([ qrCode, data ]) => {
        this.setState({
          lock: true
        })
        AntModal.info({
          title: '',
          iconType: '',
          content: (<div className="h5-bg-qr" >
            <div className="title" >H5</div>
            <img src={qrCode} alt="" />
            <div className="desc" ></div>
          </div>),
          onOK: () => {}
        })
      })
  }

  render() {
    const { pageId } = this.props
    return <div className="footer" >
      <WeChatForm ref={this.saveFormRef}
                  visible={this.state.visible}
                  onCancel={this.handleCancel}
                  onCreate={this.handleCreate} />
      <Button onClick={e => this.previewPage(pageId)} ><i className="iconfont icon-browse" /></Button>
      <Button onClick={e => this.getWeChatModal(pageId)} ><i className="iconfont icon-public" /></Button>
    </div>
  }
}

export default Publisher



