import { connect } from 'react-redux'
import H5FSDetail from '../components/H5FSDetail'
import { getQRCode, postWeChatConfig } from '../modules/fsDetail'
import { updateElement, changeTab } from '../modules/fsPreview'

const mapStateToProps = ({ previewData }) => {
  return {
    element: previewData.selectedElement,
    activeIndex: previewData.activeIndex
  }
}

const mapDispatchToProps = {
  postWeChatConfig,
  getQRCode,
  updateElement,
  changeTab,
}

export default connect(mapStateToProps, mapDispatchToProps)(H5FSDetail)



