const UPDATE_ELEMENT = 'ffan/UPDATE_ELEMENT'
const CHANGE_PRODUCT = 'ffan/CHANGE_PRODUCT'
const SELECT_ELEMENT = 'ffan/SELECT_ELEMENT'
const MOVE_ELEMENT = 'ffan/MOVE_ELEMENT'
const ADD_ELEMENT_STRUCTURE = 'ffan/ADD_ELEMENT_STRUCTURE'
const REMOVE_ELEMENT_STRUCTURE = 'ffan/REMOVE_ELEMENT_STRUCTURE'
const CHANGE_TAB = 'ffan/CHANGE_TAB'
const SET_REFERENCE = 'ffan/SET_REFERENCE'

export const moveElement = (ele) => ({
  type: MOVE_ELEMENT,
  ele
})

export const stepMoveElement = (id, step) => (dispatch, getState) => {
  const elements = getState()[ 'previewData' ][ 'elements' ]
  const selectedIndex = elements.findIndex(ele => ele.id === id)
  const targetId = elements[ selectedIndex + step ][ 'id' ]
  dispatch({
    type: MOVE_ELEMENT,
    ele: {
      fromId: id,
      toId: targetId,
    }
  })
}

export const removeElementStruct = (id) => ({
  type: REMOVE_ELEMENT_STRUCTURE,
  id,
})

export const updateElement = element => ({
  type: UPDATE_ELEMENT,
  element
})

export const selectElement = element => ({
  type: SELECT_ELEMENT,
  elementId: element.id,
  activeIndex: 1
})

export const changeTab = index => ({
  type: CHANGE_TAB,
  activeIndex: index
})

export const setRef = ref => ({
  type: SET_REFERENCE,
  ref
})

const indexById = (elements, id) => elements.findIndex(ele => ele.id === id)
const byId = (elements, id) => elements.find(ele => ele.id === id)

// @refer https://github.com/granteagon/move/blob/master/src/index.js
function move(array, moveIndex, toIndex) {
  /* #move - Moves an array item from one position in an array to another.
   Note: This is a pure function so a new array will be returned, instead
   of altering the array argument.
   Arguments:
   1. array     (String) : Array in which to move an item.         (required)
   2. moveIndex (Object) : The index of the item to move.          (required)
   3. toIndex   (Object) : The index to move item at moveIndex to. (required)
   */
  let item = array[ moveIndex ]
  let length = array.length
  let diff = moveIndex - toIndex

  if (diff > 0) {
    // move left
    return [
      ...array.slice(0, toIndex),
      item,
      ...array.slice(toIndex, moveIndex),
      ...array.slice(moveIndex + 1, length)
    ]
  } else if (diff < 0) {
    // move right
    return [
      ...array.slice(0, moveIndex),
      ...array.slice(moveIndex + 1, toIndex + 1),
      item,
      ...array.slice(toIndex + 1, length)
    ]
  }
  return array
}

const ACTION_HANDLERS = {
  [SELECT_ELEMENT]: (state, { elementId, activeIndex }) => ({
    ...state,
    selectedElementId: elementId,
    selectedElement: byId(state.elements, elementId),
    activeIndex
  }),
  [UPDATE_ELEMENT]: (state, { element }) => {
    const elements = state.elements.map(ele =>
      ele.id === element.id ? { ...ele, ...element } : ele)
    return { ...state, elements, selectedElement: byId(elements, element.id) }
  },
  [CHANGE_PRODUCT]: (state, { selectedProduct }) => {
    const { elements } = selectedProduct
    const selectedTemplateId = selectedProduct.id ? selectedProduct.id : selectedProduct.selectedTemplateId
    // TODO: detail
    return { ...state, elements, selectedTemplateId: selectedTemplateId, selectedElement: {}, selectedElementId: '' }
  },
  [MOVE_ELEMENT]: (state, { ele }) => {
    const elements = state.elements
    return { ...state, elements: move(elements, indexById(elements, ele.fromId), indexById(elements, ele.toId)) }
  },
  [ADD_ELEMENT_STRUCTURE]: (state, { elementStruct }) => {
    elementStruct.value = elementStruct.value || []
    const elements = state[ 'elements' ] || []
    return { ...state, elements: [ ...elements, elementStruct ] }
  },
  [REMOVE_ELEMENT_STRUCTURE]: (state, { id }) => {
    return { ...state, elements: state.elements.filter(ele => ele.id !== id) }
  },
  [CHANGE_TAB]: (state, { activeIndex }) => {
    return { ...state, activeIndex: activeIndex }
  },
  [SET_REFERENCE]: (state, { ref }) => {
    return { ...state, refs: { ...state.refs, ...ref } }
  }
}

const initState = {
  selectedElement: {},
  elements: [],
  activeIndex: '2',
  refs: {}
}

// import remoteReducer from './remotes'

export default function fsPreivewReducer(state = initState, action) {
  const handler = ACTION_HANDLERS[ action.type ]
  return handler ? handler(state, action) : state
}



